"use client"

import type React from "react"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

interface LeadFormProps {
  type: "car" | "bike" | "fleet" | "health" | "business" | "travel" | "contact" | "general"
  title?: string
}

export function LeadForm({ type, title = "Get Your Quote" }: LeadFormProps) {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    mobile: "",
    brand: "",
    model: "",
    year: "",
    age: "",
    nationality: "",
    company: "",
    employees: "",
    message: "",
  })

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    // Handle form submission
    console.log("Form submitted:", formData)
  }

  return (
  <div className="bg-white rounded-xl p-8 border">
    {/* Title */}
    <h3 className="text-3xl font-bold text-primary mb-8">
      Buy Insurance That Fits You….
    </h3>

    <form onSubmit={handleSubmit} className="space-y-8">
      {/* Grid Inputs */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div>
          <Label>Company Name <span className="text-red-500">*</span></Label>
          <Input
            placeholder="enter company name..."
            className="mt-2 bg-blue-50 border-none focus:ring-primary"
          />
        </div>

        <div>
          <Label>Concern Person name <span className="text-red-500">*</span></Label>
          <Input
            placeholder="Concern person name..."
            className="mt-2 bg-blue-50 border-none focus:ring-primary"
          />
        </div>

        <div>
          <Label>Mobile Number <span className="text-red-500">*</span></Label>
          <Input
            placeholder="enter mobile number..."
            className="mt-2 bg-blue-50 border-none focus:ring-primary"
          />
        </div>

        <div>
          <Label>Email Address <span className="text-red-500">*</span></Label>
          <Input
            placeholder="enter email id..."
            className="mt-2 bg-blue-50 border-none focus:ring-primary"
          />
        </div>
      </div>

      {/* Download Census */}
      <div>
        <Label>Census List Attachment</Label>
        <p className="text-primary font-medium mt-2 cursor-pointer">
          Download Census List
        </p>
      </div>

      {/* File Uploads */}
      <div className="space-y-6">
        <div>
          <Label>Census List Attachment</Label>
          <div className="flex items-center gap-4 mt-2">
            <label className="px-4 py-2 bg-gray-200 rounded-md cursor-pointer font-medium">
              Choose File
              <input type="file" className="hidden" />
            </label>
            <span className="text-muted-foreground">No file chosen</span>
          </div>
        </div>

        <div>
          <Label>Trade License Attachment</Label>
          <div className="flex items-center gap-4 mt-2">
            <label className="px-4 py-2 bg-gray-200 rounded-md cursor-pointer font-medium">
              Choose File
              <input type="file" className="hidden" />
            </label>
            <span className="text-muted-foreground">No file chosen</span>
          </div>
        </div>
      </div>

      {/* Submit Button */}
      <div className="pt-4">
        <Button
          type="submit"
          className="bg-primary hover:bg-primary/90 px-6 py-3 text-lg"
        >
          Send Application
        </Button>
      </div>
    </form>
  </div>
)
}
